.inner {
  margin: 0 auto;
  padding: 0 45px 0 60px;
  max-width: 1280px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.wrap {
  display: flex;
  min-height: 630px;
  height: calc(100vh - 81px);
}

.sidebar {
  padding-top: 40px;
  padding-bottom: 44px;
  flex: 0 0 235px;
  min-width: 235px;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.main {
  padding-top: 30px;
  flex-grow: 1;
}

.block {
  display: flex;
}

.title {
  font-family: "AvenirNext", Helvetica, sans-serif;
  font-size: 35px;
  font-weight: 500;
  line-height: 1.37;
}

.head {
  margin-bottom: 30px;
}

.header {
  padding-top: 44px;
  display: flex;
  align-items: center;
}

.header__left {
  flex: 0 0 235px;
  min-width: 235px;
  font-size: 0;
}

.header__main {
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.header__search {
  padding-right: 10px;
}

.header__account {
  font-size: 0;
}

.logo {
  position: relative;
  vertical-align: top;
  display: inline-block;
}

.logo img {
  display: inline-block;
}

.logo__text {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}

.search {
  position: relative;
  width: 470px;
  font-size: 0;
}

.search__input input {
  padding: 4px 25px 4px 4px;
  font-family: "AvenirNext", Helvetica, sans-serif;
  width: 100%;
  height: 30px;
  font-size: 14px;
  color: #4A484A;
  border-bottom: 2px solid #B0B0B0;
  display: block;
}

.search__input input::placeholder {
  font-style: italic;
  color: rgba(74, 72, 74, 0.79);
}

.search__input input:focus::placeholder {
  font-size: 0;
}

.search__button {
  position: absolute;
  top: 50%;
  right: 8px;
  margin-top: -8px;
  font-size: 0;
  background-color: transparent;
}

.search__button:before {
  position: absolute;
  content: "";
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
}

.search__button .icon {
  font-size: 15px;
  color: #114BEF;
}

.account {
  vertical-align: top;
  display: inline-block;
}

.account__top {
  display: flex;
  align-items: center;
  font-size: 0;
  background-color: transparent;
}

.account__avatar {
  position: relative;
  width: 37px;
  height: 37px;
  border-radius: 50%;
  background-position: 50% 50%;
  background-size: cover;
  background-color: #114BEF;
  border: 2px solid #114BEF;
}

.account__avatar:before {
  position: absolute;
  content: "";
  top: 100%;
  left: 50%;
  margin: -24px 0 0 -13px;
  height: 26px;
  width: 26px;
  opacity: 0.51;
  z-index: -1;
  border-radius: 50%;
  background-color: #000;
  filter: blur(5px);
}

.account__current {
  position: relative;
  margin-left: 12px;
  padding: 3px 0;
  width: 114px;
  text-align: left;
  font-family: "AvenirNext", Helvetica, sans-serif;
  cursor: pointer;
  color: #4A484A;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.33;
  border-bottom: 1px solid #8E8D8D;
  padding-right: 14px;
  background-color: transparent;
  display: block;
}

.account__name {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: block;
}

.account__arrow {
  position: absolute;
  top: 50%;
  margin-top: -4px;
  right: 0;
  font-size: 0;
}

.account__arrow .icon {
  font-size: 7px;
  color: #114BEF;
}

.megamenu__list {
  margin-bottom: -25px;
}

.megamenu__item {
  margin-bottom: 25px;
}

.item {
  position: relative;
  padding: 12px 33px 17px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  border-radius: 7px;
  min-height: 223px;
  color: #fff;
  font-size: 32px;
  height: 100%;
  background-image: linear-gradient(47deg, #A8BBD6 0%, #C7CFDB 100%);
  box-chadow: 0 3px 5px rgba(0, 0, 0, 0.16);
}

.no-touch .item:hover:before {
  opacity: 1;
}

.item:before {
  position: absolute;
  content: "";
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 7px;
  opacity: 0;
  transition: opacity .3s;
  background-image: linear-gradient(206deg, #2782F8 0%, #114BEF 95%);
}

.item__content {
  position: relative;
  z-index: 2;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.item__name {
  align-self: flex-end;
}
