// GENERATE GRID
@include generate_grid();

.inner {
	margin: 0 auto;
	padding: 0 45px 0 60px;
	max-width: 1280px;
	@include clr; }

.wrap {
	display: flex;
	min-height: 630px;
	height: calc(100vh - 81px); }

.sidebar {
	padding-top: 40px;
	padding-bottom: 44px;
	@include fls(235px);
	flex-direction: column;
	justify-content: space-between;
	display: flex;
	&__top {}
	&__bottom {} }

.main {
	padding-top: 30px;
	flex-grow: 1; }
