//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name: 'AvenirNext') {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

@font-face {
	@include fontdefault(normal, 700);
	@include fontpath('AvenirNext Bold', 'AvenirNext-Bold', AvenirNext-Bold); }
@font-face {
	@include fontdefault(italic, 700);
	@include fontpath('AvenirNext BoldItalic', 'AvenirNext-BoldItalic', AvenirNext-BoldItalic); }
@font-face {
	@include fontdefault(normal, 500);
	@include fontpath('AvenirNext Medium', 'AvenirNext-Medium', AvenirNext-Medium); }
@font-face {
	@include fontdefault(italic, 500);
	@include fontpath('AvenirNext MediumItalic', 'AvenirNext-MediumItalic', AvenirNext-MediumItalic); }
@font-face {
	@include fontdefault(normal, 400);
	@include fontpath('AvenirNext', 'AvenirNext-Regular', AvenirNext-Regular); }
@font-face {
	@include fontdefault(italic, 400);
	@include fontpath('AvenirNext Italic', 'AvenirNext-Italic', AvenirNext-Italic); }





